/*------------------------------------------------------------
btn
------------------------------------------------------------*/
.btn {
  text-align: center;
  max-width: 350px;
  margin: 0 auto;
  &__inner {
    display: inline-block;
    color: #e4002b;
    border: 1px solid #e4002b;
    border-radius: 100px;
    background: #fff;
    width: 100%;
    padding: 10px 0;
    font-size: 16px;
    font-weight: bold;
    transition: all .3s;
    position: relative;
    span {
      z-index: 1;
      position: relative;
    }
    &:before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;
      background: #e4002b;
      border-radius: 100px;
      transform: scaleX(0);
      transition:  all .3s;
      z-index: 0;
    }
    &:after {
      content: "";
      display: block;
      position: absolute;
      top: 50%;
      right: 20px;
      z-index: 1;
      width: 4px;
      height: 4px;
      border-bottom: 1px solid #e4002b;
      border-right: 1px solid #e4002b;
      transform: translate(0, -50%) rotate(-45deg);
    }
    &:hover {
      color: #fff;
      &:before {
        transform: scaleX(1);
      }
      &:after {
        border-bottom: 1px solid #fff;
        border-right: 1px solid #fff;
        transform: translate(0, -50%) rotate(-45deg);
      }
    }
  }
}
