/*------------------------------------------------------------
layout
------------------------------------------------------------*/
.wrapper {
	min-width: 960px;
	width: 100%;
	height: 100%;
	margin: auto;
	@include w768() {
		min-width: auto;
	}
}
.container {
	width: 1200px;
	margin: 0 auto;
	position: relative;
	@include w768() {
		width: 100%;
		padding: 0 20px;
	}
}
.clearfix {
	&:after {
		content: "";
		clear: both;
		display: block;
	}
}
.flex{
	display: flex;
}
.justify-center{
	justify-content: center;
}
.justify-between{
	justify-content: space-between;
}
.flex-wrapCenter {
	flex-wrap: wrap;
}
.align-center{
  align-items: center;
}
