/*------------------------------------------------------------
Component Components
------------------------------------------------------------*/
p {
	color: rgb(41, 41, 41);
	transition: all .3s;
}
.pc {
	display: block;
	@include w768(){
		display: none;
	}
}
.sp {
	display: none;
	@include w768(){
		display: block;
	}
}
.clear {
	clear: both;
}

a {
	color: #2770c3;
	font-size: 13px;
}
.text-center{
	text-align: center;
}
