/*------------------------------------------------------------
footer
------------------------------------------------------------*/
.footer {
  &__coppyright {
    background: #e4002b;
    color: #fff;
    padding: 7px 0;
  }
  &__nav {
    padding: 40px 0;
    background: #f2f2f2;
    li {
      margin-right: 22px;
      padding-right: 25px;
      border-right: 1px solid #999;
      line-height: 20px;
      &:last-child {
        border-right: none;
      }
      a {
        color: #000;
        font-weight: 600;
        font-size: 12px;
        font-family: Roboto, YakuHanJP, BaseFont-JP, Helvetica, Arial, "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", メイリオ, Meiryo, "ＭＳ Ｐゴシック", sans-serif;
      }
      &:hover {
        a {
          color: #e4002b;
        }
      }
    }
  }
}
