/*------------------------------------------------------------
Nav
------------------------------------------------------------*/
.nav-top {
  li {
    margin-right: 30px;
  }
  a {
    color: #555;
    font-weight: bold;
    font-size: 12px;
    font-family: "Roboto", "YakuHanJP", "BaseFont-JP", Helvetica, Arial, "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, "ＭＳ Ｐゴシック", sans-serif;
    letter-spacing: 2px;
    &:hover {

    }
  }
}
.header-nav {
  border-top: 1px solid #aaa;
  margin-top: 12px;
  li {
    display: inline-block;
    position: relative;
    padding: 15px 10px 15px 10px;
    &:first-child {
      padding-left: 0;
    }
    a {
      color: #555;
      font-weight: bold;
      font-size: 14px;
      font-family: "Roboto", "YakuHanJP", "BaseFont-JP", Helvetica, Arial, "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, "ＭＳ Ｐゴシック", sans-serif;
      letter-spacing: 2px;
    }
    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background: #e4002b;
      transition: all .3s;
      transform: scaleX(0);
    }
    &:hover {
      a {
        color: #e4002b;
      }
      &:after {
        transform: scaleX(1);
      }
    }
  }
}
