
//============================================
// clearfix
//============================================
@mixin clear {
	*zoom: 1;
	&:after{
		display: block;
		content: "";
		clear: both;
	}
}

//============================================
// flex
//============================================
@mixin flex {
	display: flex;
	display: -ms-flexbox;
	display: -webkit-flex;
}


//============================================
// breakpoint 1024
//============================================
@mixin w1024 {
	@media only screen and (max-width: $b_point0) {
	 @content;
	}
  }



@mixin mw768 {
	@media only screen and (min-width: $b_point1) {
	 @content;
	}
  }

//============================================
// breakpoint 768
//============================================
@mixin w768 {
	@media only screen and (max-width: $b_point1) {
	 @content;
	}
  }

//============================================
// breakpoint 480
//============================================
@mixin w480 {
	@media only screen and (max-width: $b_point2) {
	 @content;
	}
  }

//============================================
// breakpoint 499
//============================================
@mixin w499 {
	@media only screen and (max-width: $b_point3) {
	 @content;
	}
  }


