/*------------------------------------------------------------
Slides
------------------------------------------------------------*/
.mainVisual {
  &__item {
    img {
      width: 100%;
    }
  }
}

.swiper-pagination {
  position: relative;
  bottom: 0 !important;
  padding-top: 15px;
  &-bullet {
    width: 7px;
    height: 7px;
    background: #999;
  }
  &-bullet-active {
    background: red !important;
  }
}
