/*------------------------------------------------------------
Header
------------------------------------------------------------*/
.header {
  .logo {
    width: 260px;
    img {
      width: 100%;
    }
    a {
      display: block;
      width: 195px;
    }
  }
  &-mainR {
    position: relative;
    width: calc(100% - 260px);
    padding-top: 10px;
    .global-R {
      position: absolute;
      top: 10px;
      right: 0;
      a {
        padding: 8px 0 9px;
        margin: 0 16px;
        color: #555;
        font-weight: bold;
        font-size: 12px;
        font-family: "Roboto", "YakuHanJP", "BaseFont-JP", Helvetica, Arial, "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, "ＭＳ Ｐゴシック", sans-serif;
        letter-spacing: 2px;
        position: relative;
        &.global-icon {
          &:after {
            content: "";
            background: url('../images/ico_global-01.svg') center center no-repeat;
            background-size: cover;
            width: 13px;
            height: 13px;
            position: absolute;
            top: 50%;
            right: -15px;
            transform: translateY(-50%);
          }
        }
      }
    }
  }
}
